<template>
  <div class="cards page-back mx-auto" style="color: #4b4a4a">
    <h2 class="bold-x-lg mt-5 mb-5 ml-5" style="font-family: KB_C3">
      {{ this.loginInfo.user_name }} AI 
    </h2>

    <div class="text-end mb-4 mr-3">
      <button class="btn-analysis" @click="analysis">AI  </button>
    </div>

    <!--      -->
    <div
      v-if="!all"
      class="text-center"
      style="
        display: flex;
        justify-content: center;
        align-items: center;
        height: 300px;
      "
    >
      <h4 class="text-center">  .</h4>
    </div>

    <div v-else>
      <div class="row">
        <div class="col-md-3 mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div class="card-body">
              <h5 class="card-title text-goal" style="font-size: 20pt">
                 
              </h5>
              <hr />
              <p
                class="mt-4"
                style="font-size: 35pt; font-family: 'KB_C1'; font-weight: 600"
              >
                <span class="fs-4"></span> {{ myRank }}%
              </p>
              <span class="addInfo">(   : {{ dateAi }} )</span>
            </div>
          </div>
        </div>
        <div class="col-md mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div class="card-body card-ml">
              <h5 class="text-primary text-start" style="font-size: 20pt">
                AI  
              </h5>
              <p class="text-start addInfo">
                *  AI      
                   .
              </p>
              <div class="card-gray">
                <span>{{ all }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-5 mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div v-if="successYN" class="text-center">
              <div class="card-body">
                <canvas id="myChart1" style="height: 30vh"></canvas>
              </div>
              <!-- <canvas ref="makeChart1" style="height: 30vh"></canvas> -->
            </div>
          </div>
        </div>
        <div class="col-md mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div class="card-body card-ml">
              <h5 class="text-gray text-start"> </h5>
              <div class="card-gray">
                <span>{{ rank }}</span>
              </div>
              <h5 class="text-gray text-start mt-4">1   </h5>
              <div class="card-gray" style="text-align: start">
                <span> {{ catchTop }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div class="card-body card-ml">
              <h5 class="text-gray text-start"> </h5>
              <p class="text-start addInfo">
                *      , 
                        
                .
              </p>
              <div class="card-gray">
                <span> {{ mile }} </span>
              </div>
            </div>
          </div>
        </div>
        <div class="col-md-5 mb-4">
          <div class="card h-100 shadow-sm fade-in">
            <div class="card-body">
              <canvas id="myChart2" style="height: 30vh"></canvas>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md">
          <div
            class="card shadow-sm mb-4 fade-in"
            style="background-color: #f5f5f5; border: #f5f5f5"
          >
            <div class="card-body text-start card-ml">
              <h5 class="text-emphasis text-start">
                <i class="bi bi-info-circle-fill"></i>  
              </h5>
              <span class="ml-5">
                       AI 
                  . <br />
              </span>
              <span class="ml-5">
                         
                ,       .
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Chart from 'chart.js/auto';
import annotationPlugin from 'chartjs-plugin-annotation';
import api from '@/api/axios';
import Swal from 'sweetalert2';

Chart.register(annotationPlugin);

export default {
  name: 'AI_ReportView',

  data() {
    return {
      hasReport: true, //    
    };
  },
  methods: {
    async aitest() {
      try {
        const response = await api.post('bot/chat', null, {
          params: {
            prompt: `: ${this.loginInfo.user_name}, : ${this.loginInfo.user_no}`,

            user_no: this.loginInfo.user_no,
          },
        });
        console.log(response.data);
      } catch (error) {
        console.error('Error during API request:', error);
      }
    },

    async airank() {
      try {
        const response = await api.post('bot/chatRank', null, {
          params: {
            prompt: `: ${this.loginInfo.user_name}, : ${this.loginInfo.user_no}`,

            user_no: this.loginInfo.user_no,
          },
        });
        console.log(response.data);
      } catch (error) {
        console.error('Error during API request:', error);
      }
    },

    async aiCatch() {
      try {
        const response = await api.post('bot/chatCatchup', null, {
          params: {
            prompt: `: ${this.loginInfo.user_name}, : ${this.loginInfo.user_no}`,

            user_no: this.loginInfo.user_no,
          },
        });
        console.log(response.data);
      } catch (error) {
        console.error('Error during API request:', error);
      }
    },
    async aiMile() {
      try {
        const response = await api.post('bot/chatMile', null, {
          params: {
            prompt: `: ${this.loginInfo.user_name}, : ${this.loginInfo.user_no}`,

            user_no: this.loginInfo.user_no,
          },
        });
        console.log(response.data);
      } catch (error) {
        console.error('Error during API request:', error);
      }
    },

    async runAnalysis() {
      //   
      await this.aitest();
      await this.delay(5000);
      await this.airank();
      await this.delay(5000);
      await this.aiMile();
      await this.delay(10000);
      await this.aiCatch();
    },
    delay(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    ...mapActions('aireport', ['getReport']),
    // async
    getReport() {
      try {
        // await
        this.$store.dispatch('aireport/getReport', this.loginInfo.user_no);
        if (this.all) {
          //     
          this.createCharts();
        }
      } catch (error) {
        console.error('Failed to get report:', error);
        //    (:  )
      }
    },

    async createCharts() {
      this.createChart1();
      this.createChart2();
    },

    async analysis() {
      const today = new Date().toISOString().split('T')[0];
      if (this.dateAi === today) {
        Swal.fire({
          title: '',
          text: '    .',
          icon: 'info',
          confirmButtonText: '',
        });
        return;
      }

      try {
        //   ,     .
        const alertPromise = this.analysisAlert();
        const analysisPromise = this.runAnalysis();

        //      .
        await Promise.all([alertPromise, analysisPromise]);

        Swal.fire({
          title: ' ',
          text: '  .',
          icon: 'success',
          confirmButtonText: '',
        });
        //    .
        await this.getReport();

        //    .
      } catch (error) {
        console.error('   :', error);
        Swal.fire({
          title: '',
          text: '   .   .',
          icon: 'error',
          confirmButtonText: '',
        });
      }
    },

    async analysisAlert() {
      return new Promise((resolve) => {
        Swal.fire({
          timer: 45000,
          timerProgressBar: true,
          imageUrl: require('@/assets/img/analysis.gif'),
          imageClass: 'custom-image-class',
          scrollbarPadding: false,
          title: '..',
          html: `${this.getLoginInfo.user_name}  `,
          showCloseButton: true,
          didOpen: () => {
            const popup = Swal.getPopup();
            const image = Swal.getImage();
            const title = popup.querySelector('.swal2-title');
            const img = popup.querySelector('.swal2-image');

            title.style.marginTop = '0';
            title.style.paddingTop = '0';
            popup.style.height = '400px';
            img.style.margin = '15px auto 0px auto';
            image.style.width = '200px';
            image.style.height = 'auto';
            image.style.padding = '0px 0px 0px 30px';

            Swal.showLoading();
          },
          willClose: () => {
            resolve();
          },
        });
      });
    },

    createChart1() {
      console.log('1', document.getElementById('myChart1'));
      let myChart1 = document.getElementById('myChart1');
      // const myChart1 = this.$refs.makeChart1;
      const ctx = myChart1.getContext('2d');
      if (Chart.getChart(myChart1)) {
        Chart.getChart(myChart1)?.destroy();
      }

      if (!ctx) {
        return;
      }
      const score = parseInt(this.chartRank);

      const getPointColor = (score) => {
        if (score > 70) return 'rgba(146, 183, 255)';
        if (score >= 30) return 'rgba(39, 234, 182)';
        return 'rgba(255, 206, 86)';
      };

      //   
      const linearInterpolation = (x, x0, x1, y0, y1) => {
        return y0 + ((x - x0) * (y1 - y0)) / (x1 - x0);
      };

      //   x  
      const getXPosition = (score) => {
        if (score <= 30) return linearInterpolation(score, 0, 30, 0, 1);
        if (score <= 70) return linearInterpolation(score, 30, 70, 1, 2);
        return linearInterpolation(score, 70, 100, 2, 3);
      };

      const xPosition = getXPosition(score);

      myChart1 = new Chart(ctx, {
        type: 'line',
        data: {
          labels: ['0%', '30%', '70%', '100%'],
          datasets: [
            {
              data: [
                { x: 0, y: 0 },
                { x: 1, y: 30 },
                { x: 2, y: 70 },
                { x: 3, y: 100 },
              ],
              borderColor: (context) => {
                const chart = context.chart;
                const { ctx, chartArea } = chart;
                if (!chartArea) {
                  return null;
                }
                const gradient = ctx.createLinearGradient(
                  chartArea.left,
                  0,
                  chartArea.right,
                  0
                );
                gradient.addColorStop(0, 'rgba(255, 206, 86, 1)');
                gradient.addColorStop(0.31, 'rgba(255, 206, 86, 1)');
                gradient.addColorStop(0.35, 'rgba(39, 234, 182, 1)');
                gradient.addColorStop(0.66, 'rgba(39, 234, 182, 1)');
                gradient.addColorStop(0.69, 'rgba(146, 183, 255, 1)');
                gradient.addColorStop(1, 'rgba(146, 183, 255, 1)');
                return gradient;
              },
              borderWidth: 2,
              fill: true,
              backgroundColor: (context) => {
                const chart = context.chart;
                const { ctx, chartArea } = chart;
                if (!chartArea) {
                  return null;
                }
                const gradient = ctx.createLinearGradient(
                  chartArea.left,
                  0,
                  chartArea.right,
                  0
                );
                gradient.addColorStop(0, 'rgba(255, 206, 86, 0.5)');
                gradient.addColorStop(0.31, 'rgba(255, 206, 86, 0.5)');
                gradient.addColorStop(0.35, 'rgba(39, 234, 182, 0.5)');
                gradient.addColorStop(0.66, 'rgba(39, 234, 182, 0.5)');
                gradient.addColorStop(0.69, 'rgba(146, 183, 255, 0.5)');
                gradient.addColorStop(1, 'rgba(146, 183, 255, 0.5)');
                return gradient;
              },
              pointRadius: 0,
              order: 2, //    
            },
            {
              data: [{ x: xPosition, y: score }],
              pointBackgroundColor: getPointColor(score),
              pointBorderColor: getPointColor(score),
              pointRadius: 6,
              pointHoverRadius: 8,
              type: 'scatter',
              order: 1, //    
            },
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            x: {
              type: 'linear',
              min: 0,
              max: 3,
              ticks: {
                stepSize: 1,
                callback: function (value, index) {
                  return ['0%', '30%', '70%', '100%'][index];
                },
              },
            },
            y: {
              beginAtZero: true,
              max: 100,
              grid: {
                display: true,
              },
              ticks: {
                // display: false
              },
            },
          },
          plugins: {
            legend: {
              display: false,
            },
            tooltip: {
              callbacks: {
                label: function (context) {
                  if (context.datasetIndex === 1) {
                    return 'Me: ' + context.parsed.y + '%';
                  }
                },
              },
            },
          },
          elements: {
            point: {
              radius: 0,
            },
            line: {
              tension: 0.2,
            },
          },
        },
        plugins: [
          {
            afterDraw: (chart) => {
              const ctx = chart.ctx;
              const xAxis = chart.scales.x;
              const yAxis = chart.scales.y;

              const x = xAxis.getPixelForValue(xPosition);
              const y = yAxis.getPixelForValue(score);

              ctx.save();
              ctx.fillStyle = getPointColor(score);
              ctx.font = 'bold 14px Arial';
              ctx.textAlign = 'center';

              if (score < 79) {
                //  
                ctx.strokeStyle = getPointColor(score);

                //  
                ctx.beginPath();
                ctx.moveTo(x, y);
                ctx.lineTo(x, y - 40);
                ctx.lineWidth = 2;
                ctx.stroke();

                //   
                ctx.beginPath();
                ctx.moveTo(x, y - 40);
                ctx.lineTo(x - 6, y - 34);
                ctx.lineTo(x + 6, y - 34);
                ctx.closePath();
                ctx.fill();

                // "Me!"  
                ctx.fillText('Me!', x, y - 50);
              } else {
                // score 79   scatter   "Me!" 
                ctx.fillText('Me!', x, y - 15);
              }

              ctx.restore();
            },
          },
        ],
      });
    },

    createChart2() {
      let myChart2 = document.getElementById('myChart2');

      const ctx = myChart2.getContext('2d');
      if (Chart.getChart(myChart2)) {
        Chart.getChart(myChart2)?.destroy();
      }

      if (!ctx) {
        return;
      }

      this.label = this.avg_score_json.map((item) => item.mile_name);
      this.avg_data = this.avg_score_json.map((item) => item.average_score);
      this.my_data = this.my_score_json.map((item) => item.total_score);

      console.log();
      let data = {
        labels: this.label,

        datasets: [
          {
            label: ' ',
            data: this.my_data,
            backgroundColor: 'rgba(255, 0, 46, 0.3)',
            borderColor: 'rgba(255, 0, 46, 0.5)',
            pointBackgroundColor: 'rgb(255, 99, 132)',
            pointBorderColor: '#fff',
            pointHoverBackgroundColor: '#fff',
            pointHoverBorderColor: 'rgb(255, 99, 132)',
          },
          {
            label: ' ',
            data: this.avg_data,
            backgroundColor: 'rgba(0, 122, 255, 0.2)',
            borderColor: 'rgba(0, 122, 255, 0.4)',
            pointBackgroundColor: 'rgb(54, 162, 235)',
            pointBorderColor: '#fff',
            pointHoverBackgroundColor: '#fff',
            pointHoverBorderColor: 'rgb(54, 162, 235)',
          },
        ],
      };

      const config = {
        type: 'radar',
        data: data,
        options: {
          elements: {
            line: {
              borderWidth: 3,
            },
          },
          scales: {
            r: {
              angleLines: {
                display: false,
              },
              suggestedMin: 0,
              suggestedMax: 100,
              ticks: {
                stepSize: 20, //  20  (5 : 20, 40, 60, 80, 100)
                display: false, //    
              },
              pointLabels: {
                font: {
                  size: 12, //    
                },
              },
              grid: {
                // circular: true,
                color: 'rgba(0, 0, 0, 0.2)', //     ()
              },
            },
          },
        },
      };

      myChart2 = new Chart(ctx, config);
    },
  },
  mounted() {
    this.getReport();
  },
  computed: {
    ...mapGetters('login', ['getLoginInfo']),

    ...mapGetters('aireport', [
      'getAll',
      'getRank',
      'getCatchTops',
      'getDateAi',
      'getMyRank',
      'getChartRank',
      'getAvg_score_json',
      'getMy_score_json',
      'getSuccessYN',
      'getMile',
    ]),

    loginInfo() {
      return this.getLoginInfo;
    },
    all() {
      return this.getAll;
    },
    rank() {
      return this.getRank;
    },
    catchTop() {
      return this.getCatchTops;
    },
    mile() {
      return this.getMile;
    },
    dateAi() {
      return this.getDateAi;
    },
    myRank() {
      return this.getMyRank;
    },
    chartRank() {
      return this.getChartRank;
    },
    avg_score_json() {
      return this.getAvg_score_json;
    },
    my_score_json() {
      return this.getMy_score_json;
    },
    successYN() {
      return this.getSuccessYN;
    },
  },

  watch: {
    loginInfo: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          this.getReport();
        }
      },
    },
    all: {
      handler(newVal) {
        if (newVal) {
          this.$nextTick(() => {
            this.createCharts();
          });
        }
      },
      immediate: true,
    },
  },
};
</script>

<style scoped>
.text-emphasis {
  font-size: 13pt;
  font-family: 'KB_S5';
  font-weight: 600;
  color: #4b4a4a;
}

.card {
  transition: all 0.3s ease;
}

.card-ml {
  margin-left: 10px;
  text-align: start;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
}

.text-primary {
  color: #32ab8b !important;
  font-weight: 600;
  font-family: 'KB_S5';
}

.text-secondary {
  color: #4a90e2 !important;
  font-weight: 600;
  font-family: 'KB_S5';
}

.text-goal {
  color: #ff7e5e !important;
  font-weight: 600;
  font-family: 'KB_S5';
}

.text-gray {
  color: #3b3b3b !important;
  font-weight: 600;
  font-family: 'KB_S5';
}

.addInfo {
  color: rgb(152, 152, 152);
  font-size: 12pt;
}

.card-gray {
  border-radius: 7px;
  padding: 15px 10px 15px 10px;
  text-align: start;
}

span {
  font-size: 14pt;
  font-family: 'KB_S5';
}

.row {
  margin: 17px 0px 17px 0px;
}

.highlight {
  background-color: #fff6d4;
  border-radius: 30px; /*   */
  padding: 4px 8px; /*      */
  display: inline-block; /*       */
}

.page-back {
  padding-left: 5%;
  padding-right: 5%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-out;
}
</style>
